
<template>
  <!--
    /**
    /*1:<flip @customEvent='customEvent' :Body='patientBody' :Condition='condition'></flip>
    /*2customEventmethodscustomEvent(Condition){}  
    /*3Bodybody
    /*4Condition       
  -->
  <div class='flip'>
    <div class='flipBtn'>
      <div class="btn-group pull-right" role="group" style='margin-left:10px;' aria-label="...">
        <button type="button" class="btn btn-default" @click='homePage'></button>
        <button type="button" class="btn btn-default" @click='previousPage'></button>
        <button type="button" class="btn btn-default">{{Body.pages==0?0:Body.pageNum}}/{{Body.pages}}</button>
        <button type="button" class="btn btn-default" @click='nextPage'></button>
        <button type="button" class="btn btn-default" @click='lastPage'></button>
      </div>
      <div class="btn-group pull-right" role="group" aria-label="...">
        <button type="button" class="btn btn-default">10</button>
        <button type="button" class="btn btn-default">{{Body.pages}}</button>
      </div>
    </div>
  </div>
</template>
<script>  
export default {
  data() {
    return {}
  },
  methods: {
    homePage() {
      this.Condition.pageNumber = 1
      this.$emit('customEvent', this.Condition)
    },
    lastPage() {
      this.Condition.pageNumber = this.Body.pages
      this.$emit('customEvent', this.Condition)
    },
    previousPage() {
      if (this.Condition.pageNumber <= 1) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return
      }
      this.Condition.pageNumber--
      this.$emit('customEvent', this.Condition)
    },
    nextPage() {
      if (this.Condition.pageNumber >= this.Body.pages) {
        this.$message({
          message: '',
          type: 'warning'
        })
        return
      }
      this.Condition.pageNumber++
      this.$emit('customEvent', this.Condition)
    },
  },
  props: ['Body', 'Condition']
}
</script>
<style scoped>
.flip .flipBtn {
  text-align: center;
}
</style>


